
var touchStart = false;
var touchStartPosition = { x: 0, y: 0 };
var swipeDirection = { x: 0, y: 0 };
var minSwipeLength = 50;
var initialized = false;

export default class SwipeInput {


    static initialize() {
        if (!initialized) {
            window.addEventListener("mousedown", (e) => {
                SwipeInput.start(e);
            });
            window.addEventListener("mousemove", (e) => {
                if (touchStart && e.button == 0) {
                    swipeDirection = { x: e.screenX - touchStartPosition.x, y: e.screenY - touchStartPosition.y }
                }
            });
            window.addEventListener('mouseup', (e) => {
                SwipeInput.stop();
            });
            initialized = true;
        }
    }

    static start(e) {
        if (e.button == 0) {
            touchStart = true;
            touchStartPosition.x = e.screenX;
            touchStartPosition.y = e.screenY;
        }
    }

    static stop() {
        touchStart = false;
        swipeDirection = { x: 0, y: 0 };
        touchStartPosition = { x: 0, y: 0 };
    }


    static onDirection(dir, callback) {

        SwipeInput.initialize();
        window.addEventListener('mousemove', (e) => {
            let confirm = false;

            if(dir.x!=0) {
                if(dir.x<0) {
                    if(swipeDirection.x < dir.x)
                        confirm=true;
                } else {
                    if(swipeDirection.x > dir.x)
                        confirm=true;
                }
            }

            if(dir.y!=0) {
                if(dir.y<0) {
                    if(swipeDirection.y < dir.y)
                        confirm=true;
                } else {
                    if(swipeDirection.y > dir.y)
                        confirm=true;
                }
            }

            if (touchStart && confirm) {
                callback();
                SwipeInput.stop();
            }
        });
    }


}


